
.catalog-container {
  padding-top: 80px;
  padding-bottom: 20px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  background-color: #eee;
  @include r(768) {
    padding-top: 0;
    padding-left: 0px;
    padding-right: 0px; }

  .col-12, .col-lg-4, .col-sm-6 {
    padding-left: 0;
    padding-right: 0; }

  .row {
    margin: 0;
    @include r(768) {
      padding: 0 20px; } }

  & .row > div {
    padding-right: 20px;
    padding-bottom: 20px;
    @include r(991) {
      padding-right: 10px;
      padding-bottom: 10px;
      @include r(575) {
        padding-right: 0; } } }

  & .row > div:nth-child(2n) {
    @include r(991) {
      padding-right: 0px; } } }



.catalog-seleted-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px; }

.catalog-seleted-left {
  display: flex;
  width: 100%;
  max-width: 530px;
  @include r(800) {
    max-width: 500px;
    @include r(768) {
      width: 50%;
      max-width: 50%;
      background-color: #000;
      border: 1px solid #333;
      border-left: 0px;
      position: relative; } } }

.select-compare {
  width: 100%;
  max-width: 100%;
  background-color: #000;
  position: fixed;
  bottom: 0;
  left: 0;
  opacity: 0;
  transition: opacity .8s;
  z-index: 1; }


.select-compare.active {
  opacity: 1; }


.select-compare-title {
  a {
    display: block;
    @include fontSR;
    font-size: 1.4rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #fff;
    padding: 20px;
    position: relative;
    width: 100%;
    @include r(380) {
      font-size: 1.2rem;
      padding-left: 15px; }
    &::before {
      content: '';
      position: absolute;
      top: calc(50% - 5px);
      right: 20px;
      width: 10px;
      height: 10px;
      @include img;
      background-image: url('../img/catalog-page/arrow-up-mob.svg'); } } }


.catalog-seleted-right {
  max-width: 300px;
  @include r(768) {
    width: 50%;
    max-width: 50%;
    background-color: #000;
    border: 1px solid #333;
    border-left: 0px;
    border-right: 0px; } }


.catalog-seleted-left-fixed,
.catalog-seleted-right-fixed {
  display: none;
  transition: opacity 1s;
  z-index: 5; }

.catalog-seleted-left-fixed {
  @include r(768) {
    position: fixed;
    top: 75px;
    left: 0;
    opacity: 0;
    display: block; } }

.catalog-seleted-right-fixed {
  @include r(768) {
    position: fixed;
    top: 75px;
    right: 0;
    opacity: 0;
    display: block; } }

.catalog-seleted-right-fixed.active,
.catalog-seleted-left-fixed.active {
  opacity: 1; }


.select-filter {
  width: 100%;
  display: none;
  @include r(768) {
    display: block; } }

.select-filter-title {
  @include fontSR;
  font-size: 1.4rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-decoration-line: none;
  text-transform: uppercase;
  color: #fff;
  padding: 20px;
  position: relative;
  width: 100%;
  @include r(380) {
    font-size: 1.2rem;
    padding-left: 15px; }

  &::before {
    content: '';
    position: absolute;
    top: calc(50% - 5px);
    right: 20px;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/catalog-page/arrow-down-mob.svg'); } }



.btn-sort {
  width: 100%;
  position: relative;
  padding: 3px 0;
  @include r(768) {
    padding: 0; }
  button {
    display: block;
    outline: none;
    border: none;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #000;
    padding-right: 30px;
    background-color: transparent;
    @include r(920) {
      font-size: 1.4rem;
      @include r(768) {
        color: #fff;
        padding: 20px;
        width: 100%;
        text-align: left;
        font-size: 1.4rem;
        @include r(380) {
          font-size: 1.2rem;
          padding-left: 15px; } } } }



  .sort-up {
    position: absolute;
    top: 0;
    right: 0;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/main-page/icons/arrow-left.svg');
    transform: rotate(90deg);
    @include r(768) {
      background-image: url('../img/catalog-page/arrow-up-mob.svg');
      transform: rotate(0deg);
      top: 17px;
      right: 15px; } }

  .sort-down {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/main-page/icons/arrow-right.svg');
    transform: rotate(90deg);
    @include r(768) {
      background-image: url('../img/catalog-page/arrow-down-mob.svg');
      transform: rotate(0deg);
      bottom: 13px;
      right: 15px; } } }

.arrow-active {
  opacity: 1; }

.arrow-disabled {
  opacity: .4; }





// SELECT


.select__color__top,
.select__gender__top,
.select__subcategory__top {
  display: block; }

.seleted-color,
.seleted-gender,
.seleted-subcategory {
  display: flex;
  width: 100%;
  max-width: 120px;
  cursor: pointer;
  position: relative;
  align-items: center;
  margin-right: 35px;
  @include r(920) {
    max-width: 110px;
    @include r(768) {
      display: none; } }

  & a {
    display: block;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #000;
    height: 100%;
    text-decoration: none;
    transition: all .5s;
    @include r(920) {
      font-size: 1.4rem; }

    &:hover {
      text-decoration: underline; } } }


.select__body-color,
.select__body-gender,
.select__body-subcategory {
  display: none;
  position: absolute;
  top: 100%;
  width: 110%;
  left: -10px;
  z-index: 2;
  padding: 25px 20px;
  border: 1px solid #000;
  background-color: #fff; }


.link__color,
.link__gender,
.link__subcategory {
  position: relative;
  padding: 10px 0;
  box-sizing: border-box; }

.seleted-subcategory {
  max-width: 220px;
  margin-right: 0;
  @include r(920) {
    max-width: 150px; } }






// js-select
// COLOR
.seleted-color.is-active .select__body-color {
  display: block; }


.select-color__top {
  @include r(768) {
    position: relative;
    width: 100%; } }

.select-color__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 5px );
  right: 30px;
  width: 10px;
  height: 10px;
  background-image: url('../img/history-page/arrow-select.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: transform .4s; }

.seleted-color.is-active .select-color__top:before {
  transform: rotate(180deg); }

// GENDER

.seleted-gender.is-active .select__body-gender {
  display: block; }

.select-gender__top {
  @include r(768) {
    position: relative;
    width: 100%; } }

.select-gender__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 5px );
  right: 20px;
  width: 10px;
  height: 10px;
  background-image: url('../img/history-page/arrow-select.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: transform .4s; }

.seleted-gender.is-active .select-gender__top:before {
  transform: rotate(180deg); }

// subcategory

.seleted-subcategory.is-active .select__body-subcategory {
  display: block; }

.select-subcategory__top {
  @include r(768) {
    position: relative;
    width: 100%; } }

.select-subcategory__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 5px );
  right: 50px;
  width: 10px;
  height: 10px;
  background-image: url('../img/history-page/arrow-select.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: transform .4s;
  @include r(920) {
    right: 0; } }

.seleted-subcategory.is-active .select-subcategory__top:before {
  transform: rotate(180deg); }




// drop select

.drop-select-wrapper {
  display: none;
  background-color: #000;
  border-right: 1px solid #000;
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 50px;
  position: absolute;
  top: 101%;
  width: 201%;
  left: 0;
  z-index: 1; }

.drop-select-text {
  @include fontSR;
  font-size: 2rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF; }


.drop-select-small-text {
  @include fontSR;
  font-size: 1.6rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding-left: 20px; }


.subcategory,
.color,
.gender {
  width: 100%;
  position: relative;
  padding-bottom: 30px;

  &::before {
    content: '';
    position: absolute;
    top: 7px;
    right: 20px;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/catalog-page/arrow-down-mob.svg'); } }

.color,
.gender {
  &::before {
    background-image: none; } }

.drop-subcategory {
  display: none;
  padding-top: 30px;
  li {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0; } } }






.chek-box {
  display: flex;
  align-items: center;
  margin-bottom: 30px; }

.chek-box-end {
  margin-bottom: 0px; }


.checkbox-login01 {
  display: none; }

.style-login01 {
  display: inline-block;
  height: 20px;
  width: 20px;
  border: 2px solid #FFFFFF;
  background-color: transparent;
  position: relative;
  margin-right: 10px;
  cursor: pointer;
  &::before {
    content: '';
    position: absolute;
    color: #fff;
    font-size: 10px;
    height: 20px;
    width: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: .3s; } }


.chek-box-text {
  @include fontSR;
  font-size: 1.2rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer;
  padding-left: 30px; }

//       
.checkbox-login01:checked + .style-login01::before {
  opacity: 1; }


.e-bike {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 3px;
    width: 20px;
    height: 20px;
    @include img;
    background-image: url('../img/catalog-page/zipper.svg'); } }

.sale {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 5px;
    left: 3px;
    width: 15px;
    height: 15px;
    @include img;
    background-image: url('../img/catalog-page/percent.svg'); } }

.new {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 3px;
    width: 20px;
    height: 20px;
    @include img;
    background-image: url('../img/catalog-page/sun.svg'); } }





// HOVER
.subcategory:hover .drop-subcategory {
  display: block; }


.catalog-seleted-left:hover .drop-select-wrapper {
  display: block !important; }



.catalog-container {
  .sale-bike-price {
    display: inline-block;
    font-weight: bold; }

  .race-slide {
    height: 100%;
    @include r(1040) {
      max-width: 100%; } }

  .race-slide img {
    @include r(1040) {
      max-height: 400px;
      height: auto; } } }


.price-discount {
  display: inline-block;
  @include fontFR;
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 120%;
  letter-spacing: 0.1em;
  text-decoration: line-through;
  color: #808080;
  @include r(380) {
    font-size: 1.4rem; } }

.sale-colors {
  position: absolute;
