.race-page-main {
  width: 100%;
  padding-top: 160px;
  padding-left: 40px;
  padding-bottom: 635px;
  position: relative;
  @include img;
  background-image: url('../img/main-page/content-1.jpeg');
  @include r(1300) {
    padding-bottom: 550px;
    @include r(1070) {
      padding-bottom: 450px;
      @include r(768) {
        padding-bottom: 410px;
        @include r(580) {
          padding-left: 10px;
          padding-right: 10px;
          padding-bottom: 410px;
          @include r(380) {
            padding-bottom: 330px; } } } } } }


.race__page-main-title {
  max-width: 540px;
  @include r(768) {
    max-width: 440px;
    @include r(580) {
      max-width: 350px;
      margin: 0 auto;
      padding-bottom: 20px; } } }

.race__page-main-subtitle {
  max-width: 420px;
  @include r(768) {
    max-width: 350px;
    @include r(580) {
      margin: 0 auto;
      padding: 0 45px 0 0; } }
  p {
    @include r(768) {
      font-size: 1.8rem; } } }

.race-slide-content {
  flex-shrink: 1; }


.race-slide {
  @include r(1040) {
    max-width: 340px;
    @include r(380) {
      max-width: 300px; } } }



.race__slider-wrap {
  width: 100%;
  height: 200px;
  background-color: #f0ecec;
  padding: 0 40px;
  position: absolute;
  bottom: 0;
  left: 0;
  @include r(1300) {
    height: 150px;
    @include r(1070) {
      height: 100px;
      @include r(580) {
        height: 300px;
        @include r(380) {
          height: 200px; } } } } }

.race__slider-wrapper {
  @include r(768) {
    max-height: 325px;
    @include r(380) {
      max-height: 255px; } } }

.race__slider-container {
  width: 97%;
  position: absolute;
  bottom: 40px;
  left: calc( 50% - 49% );
  @include r(1040) {
    max-width: 710px;
    left: 50%;
    transform: translateX(-50%);
    @include r(768) {
      max-width: 350px;
      bottom: 50px;
      @include r(380) {
        max-width: 310px; } } } }

.race-slide {
  box-sizing: border-box;
  background-color: #fff;
  padding: 20px;
  margin-right: 19px;
  width: 100%;
  height: auto;
  @include r(1040) {
    height: 100%;
    @include r(580) {
      padding: 15px; } }
  a {
    display: block; }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include r(1040) {
      display: block;
      width: 100%;
      max-width: 280px;
      height: 100%;
      max-height: 150px;
      margin: 0 auto;
      @include r(768) {
        height: 175px;
        max-width: 300px;
        @include r(380) {
          height: 120px;
          max-width: 230px; } } } } }



.sale-bike-add {
  display: flex;
  justify-content: flex-end;
  button {
    display: block;
    border: none;
    outline: none;
    @include fontSR;
    font-size: 1.4rem;
    line-height: 200%;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #000000;
    cursor: pointer;
    background-color: transparent;
    margin-bottom: 40px;
    @include r(1070) {
      margin-bottom: 20px;
      @include r(580) {
        font-size: 1.4rem;
        margin-bottom: 35px;
        @include r(380) {
          margin-bottom: 20px; } } } } }

.sale-bike-price {
  @include fontFR;
  font-size: 1.8rem;
  line-height: 120%;
  letter-spacing: 0.1em;
  @include r(380) {
    font-size: 1.4rem; }
  b {
    @include fontSR;
    font-size: 2.4rem;
    display: block;
    padding-bottom: 10px;
    padding-top: 35px;
    @include r(580) {
      padding-top: 30px;
      @include r(380) {
        padding-top: 10px;
        font-size: 2rem; } } } }




.race-slide-end {
  margin-right: 0 !important;
  padding-top: 40px;
  flex-shrink: 1;
  display: flex;
  align-items: center;
  @include r(1040) {
    flex-shrink: 0;
    width: 100%;
    max-width: 400px;
    height: auto; } }

.race-slide-review-img {
  max-width: 135px;
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  @include r(1300) {
    max-width: 100px;
    @include r(1230) {
      margin-bottom: 15px;
      @include r(1040) {
        max-width: 80px;
        margin-bottom: 10px; } } }
  img {
    @include r(768) {
      height: auto; } } }


.race-slide-review {
  width: 100%;
  margin: 0 auto; }

.race-slide-review-title {
  @include fontSM;
  font-size: 2.4rem;
  line-height: 200%;
  letter-spacing: 0.12rem;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 12px;
  text-align: center;
  @include r(1230) {
    font-size: 2.2rem;
    padding-bottom: 10px;
    line-height: 150%;
    @include r(580) {
      font-size: 2rem; } } }

.race-slide-review-subtitle {
  @include fontSR;
  font-size: 1.8rem;
  line-height: 200%;
  letter-spacing: 0.12rem;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 16px;
  margin: 0 auto;
  text-align: center;
  max-width: 250px;
  @include r(1230) {
    padding-bottom: 10px;
    font-size: 1.6rem;
    max-width: 200px;
    @include r(580) {
      font-size: 1.4rem; } } }

.race-slide-review-btn {
  max-width: 130px;
  margin: 0 auto;
  @include r(580) {
    max-width: 105px; }
  button {
    display: block;
    border: none;
    outline: none;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 200%;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #000000;
    cursor: pointer;
    background-color: transparent;
    margin-bottom: 40px;
    text-decoration: underline;
    @include r(1230) {
      margin-bottom: 20px;
      font-size: 1.4rem;
      @include r(580) {
        font-size: 1.3rem; } } } }


.swiper-pagination-race {
  display: none;
  @include r(1040) {
    display: block;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%);
    z-index: 1;
    @include r(768) {
      bottom: 20px; } }
  .swiper-pagination-bullet {
    width: 70px;
    height: 4px;
    border-radius: 0;
    background-color: #fff;
    opacity: 1;
    @include r(768) {
      height: 6px;
      width: 68px;
      @include r(410) {
        width: 40px;
        height: 4px; } } }
  .swiper-pagination-bullet-active {
    background-color: #000; } }
