.face__page-experts {
  width: 100%;
  height: 100%;
  max-width: 100%;
  padding-bottom: 95px;
  padding-left: 25px;
  padding-right: 25px;
  @include r(768) {
    padding-bottom: 60px;
    padding-left: 5px;
    padding-right: 5px;
    @include r(380) {
      padding-bottom: 20px; } }

  .col-sm-6 {
    padding-bottom: 40px;
    @include r(380) {
      padding-bottom: 30px; } } }


.face__page-experts-img {
  margin-bottom: 20px;
  @include r(575) {
    max-height: 375px; }
  img {
    width: 100%;
    height: auto; } }

.face__page-experts-address {
  @include fontFR;
  font-size: 1.4rem;
  line-height: 150%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.5);
  @include r(1240) {
    font-size: 1.2rem;
    @include r(1140) {
      font-size: 1rem; } } }

.face__page-experts-title {
  @include fontSR;
  font-size: 2.4rem;
  line-height: 170%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  @include r(1240) {
    font-size: 2rem;
    @include r(1140) {
      font-size: 1.8rem;
      @include r(880) {
        font-size: 1.6rem; } } } }

.face__page-experts-subtitle {
  a {
    @include fontSR;
    font-size: 1.6rem;
    line-height: 200%;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #000000;
    @include r(1240) {
      font-size: 1.4rem;
      @include r(1140) {
        font-size: 1.2rem; } } } }


.col-page-experts {
  @include r(575) {
    max-width: 375px;
    margin: 0 auto; } }
