.page-history-2007 {
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: flex-end;
  position: relative;
  @include r(768) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 40px;
    padding-bottom: 40px; } }

.page-history-2007-wrapper {
  width: 100%;
  max-width: 100%; }

.page-history-2007-text {
  max-width: 885px;
  margin-left: auto;
  @include r(1240) {
    max-width: 740px;
    @include r(1040) {
      max-width: 600px;
      @include r(920) {
        max-width: 490px;
        @include r(768) {
          max-width: 100%; } } } } }

.page-history-img-wrapper-2007 {
  max-width: 100%;
  display: flex;
  @include r(768) {
    flex-direction: column; }

  div {
    margin-right: 20px;
    @include r(768) {
      margin: 0 auto; }
    &:last-child {
      margin-right: 0; } }
  span {
    display: none;
    padding-bottom: 40px;
    width: 100%;
    max-width: 100%;
    @include r(768) {
      display: block; }
    &:last-child {
      padding-bottom: 0; } } }



