

.header {
  position: fixed;
  z-index: 1;
  background-color: transparent;
  width: 100%;
  padding: 30px 40px;
  padding-bottom: 0;
  @include r(1070) {
    padding: 20px;
    padding-bottom: 0;
    @include r(768) {
      padding: 25px 20px;
      padding-bottom: 0; } } }


.header-black {
  background-color: #000; }

.header__row {
  display: flex;
  justify-content: space-between; }


.header__left, .header__right {
  display: flex;
  align-items: center; }


.header__right {
  padding-bottom: 30px;
  @include r(768) {
    display: none; } }





.header__logo {
  margin-right: 23px;
  max-width: 184px;
  padding-bottom: 30px;
  @include r(1070) {
    max-width: 150px;
    max-height: 50px;
    margin-right: 13px;
    @include r(820) {
      margin-right: 0px; } }
  a {
    display: block; }
  img {
    width: 100%;
    height: 100%; } }


.menu {
  height: 100%;
  @include r(768) {
    position: fixed;
    top: 0;
    right: -100%;
    z-index: 99;
    width: 100%;
    height: 100%;
    background-color: #000;
    transition: right .4s; }

  &::before {
    display: none;
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 200px;
    height: 200px;
    z-index: 99;
    background-image: url('../img/main-page/icons/arrow-big-menu.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include r(768) {
      display: block; } } }


.menu__list {
  display: flex;
  height: 100%;
  @include r(768) {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 50px;
    padding-bottom: 34px;
    margin: 0 auto;
    padding-left: 25px;
    height: auto; }

  li {

    cursor: pointer;
    display: flex;
    align-items: center;
    margin-left: 40px;
    padding-bottom: 30px;
    @include r(1070) {
      margin-left: 20px;
      @include r(820) {
        margin-left: 15px;
        @include r(768) {
          margin-left: 0;
          display: block;
          text-align: left; } } } } }

.menu__link {
  @include fontSR;
  text-decoration: none;
  font-size: 1.6rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #fff;
  position: relative;
  @include r(1070) {
    font-size: 1.4rem; }


  &:hover {
    text-decoration: underline;
    @include r(768) {
      text-decoration: none; } } }

.find-menu {
  display: none !important;
  @include r(768) {
    display: block !important; }
  a {
    display: none;
    @include r(768) {
      display: block;
      @include fontSR;
      text-decoration: none;
      font-size: 1.6rem;
      line-height: 105%;
      letter-spacing: 0.12em;
      text-transform: uppercase;
      color: #fff;
      position: relative;
      @include r(1070) {
        font-size: 1.4rem; } } } }

// arrow down menu

.bikes-arrow {
  &::before {
    @include r(768) {
      content: '';
      position: absolute;
      top: calc( 50% - 3px );
      right: -15px;
      width: 5px;
      height: 5px;
      background-image: url('../img/main-page/icons/arrow-dropmenu.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      transform: rotate(180deg);
      transition: transform .3s; } } }

.about-arrow {
  &::before {
    @include r(768) {
      content: '';
      position: absolute;
      top: calc( 50% - 3px );
      right: -15px;
      width: 5px;
      height: 5px;
      background-image: url('../img/main-page/icons/arrow-dropmenu.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      transform: rotate(180deg);
      transition: transform .3s; } } }



// burger
.header-btn {
  display: none;
  cursor: pointer;
  @include r(768) {
    display: block;
    z-index: 99;
    transition: all .4s; } }

.header__burger {
  position: relative;
  width: 40px;
  height: 20px;
  z-index: 1;
  @include r(768) {
    width: 22px;
    height: 20px; }

  & span {
    position: absolute;
    top: 9px;
    right: 0;
    width: 100%;
    height: 3px;
    background-color: #fff;
    @include r(768) {
      height: 2px; } } }


.header__burger::before, .header__burger::after {
  content: '';
  position: absolute;
  right: 0;
  height: 3px;
  background-color: #fff;
  transition: all .3s; }

.header__burger::before {
  width: 100%;
  top: 0;
  @include r(768) {
    height: 2px; } }

.header__burger::after {
  width: 65%;
  bottom: 0;
  @include r(768) {
    height: 2px; } }





.header__find {
  a {
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: underline;
    text-transform: uppercase;
    color: #fff;
    position: relative;
    padding-left: 25px;
    @include r(1070) {
      font-size: 1.4rem; }

    &::before {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url('../img/main-page/icons/marker.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center; } } }

.header__search {
  margin: 0 37px;
  max-height: 20px;
  position: relative;
  overflow: hidden;
  @include r(1070) {
    margin: 20px; }
  button {
    background-color: transparent;
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    display: block;
    img {
      width: 100%;
      height: 100%; } }
  input {
    opacity: 0;
    transition: opacity .4s;
    @include fontSR;
    font-size: 1.6rem;
    display: block;
    position: absolute;
    top: 50px;
    height: 40px;
    right: 0;
    padding: 0 15px;
    width: 230px;
    outline: none;
    border: 1px solid #fff;
    background-color: rgba(#000, .5 );
    color: #fff;
    @include r(1070) {
      font-size: 1.4rem;
      width: 190px;
      height: 35px;
      @include r(768) {
        display: none; } }

    &::placeholder {
      @include fontSR;
      opacity: .8;
      color: #fff;
      font-size: 1.7rem;
      @include r(1070) {
        font-size: 1.5rem; } } } }


.header__search.active {
  overflow: visible; }

#search-header.active {
  opacity: 1; }



.header__basket {
  margin-right: 30px;
  @include r(1070) {
    margin-right: 20px; }
  a {
    display: block; } }



// SELECT
.select__language-top {
  display: block; }

.header__select-language {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;

  & a {
    display: block;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
    transition: all .5s;
    @include r(1070) {
      font-size: 1.4rem; }

    &:hover {
      text-decoration: underline; } } }

.select__body-language {
  display: none;
  position: absolute;
  top: 113%;
  left: 0;
  z-index: 2; }

.link__language {
  position: relative;
  padding: 10px 0;
  box-sizing: border-box; }

.select-menu-head {
  @include r(768) {
    display: block; } }



// Icons menu

.menu__botoom-icons {
  display: none;
  a {
    display: block;
    margin-left: 25px; } }

.menu.active .menu__botoom-icons {
  display: flex;
  justify-content: center;
  max-width: 500px;
  margin: 0 auto;
  margin-bottom: 40px; }






// MENU MOB HEAD
.input-serch-menu {
  height: 40px;
  position: relative;
  width: 100px;
  opacity: .6;
  transition: all .4s;
  form {
    height: 100%; }
  button {
    background-color: transparent;
    outline: none;
    border: none;
    position: absolute;
    top: calc(50% - 10px);
    right: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.6;
    img {
      width: 100%;
      height: 100%; } }

  &:hover {
    opacity: 1;
    width: 255px;
    border-bottom: 1px solid #FFFFFF;
    background-color: #191919;
    @include r(400) {
      width: 200px;
      @include r(360) {
        width: 170px; } } } }



.menu-mob-head {
  display: none;
  @include r(768) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 15px 75px 15px 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

  input {
    width: 100%;
    border: none;
    outline: none;
    background-color: transparent;
    height: 100%;
    border-bottom: 1px solid transparent;
    transition: all .4s;
    padding: 0 35px 0 10px;
    @include fontFR;
    font-size: 1.8rem;
    line-height: 150%;
    letter-spacing: 0.1em;
    color: #FFFFFF !important; }

  input[placeholder] {
    @include fontFR;
    font-size: 1.8rem;
    line-height: 120%;
    letter-spacing: 0.06em;
    color: #929191; } }





// menu scroll
