// JS


// js-select
.header__select-phone.is-active .select__body-phone,
.header__select-language.is-active .select__body-language {
  display: block; }

.select__item-phone.remove {
  display: none; }

.select__item-language.remove {
  display: none; }


// js-menu

.header__burger.active {
  position: fixed;
  right: 20px; }


.header__burger.active::before {
  transform: rotate(45deg);
  top: 7px;
  right: 5px; }


.header__burger.active::after {
  transform: rotate(-45deg);
  top: 7px;
  right: 5px;
  width: 100%; }

.header__burger.active span {
  display: none; }


.menu.active {
  right: 0;
  overflow: auto; }

.menu.active .select-language__top {
  @include r(768) {
    position: relative; } }

.menu.active .select-language__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 3px );
  right: -50%;
  width: 5px;
  height: 5px;
  background-image: url('../img/main-page/icons/arrow-dropmenu.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transform: rotate(180deg);
  transition: transform .4s; }

.header__select-language.is-active .select-language__top:before {
  transform: rotate(0deg); }



// CSS HOVER

.bikes:hover .wrapper__drop {
  display: flex;
  @include r(768) {
    display: block; } }


.about:hover .dropmenu-about {
  display: flex;
  @include r(768) {
    display: block; } }


.bikes:hover .menu__dropdown-1 {
  display: block; }


.mountain:hover .menu__dropdown-2 {
  display: block;
  @include r(768) {
    display: flex; } }

.all-terrain:hover .menu__dropdown-3 {
  display: block;
  @include r(768) {
    display: flex; } }

.plus1311:hover .menu__dropdown-4 {
  display: block;
  @include r(768) {
    display: flex; } }

.drop__img.active {
  display: flex;
  @include r(768) {
    display: none; } }
