
.teams-block-2 {
  width: 100%;
  height: 100%;
  max-width: 100%;
  padding: 40px;
  padding-top: 0;
  @include r(768) {
    padding: 20px;
    padding-top: 0; } }

.block-2-wrapper {
  padding-left: 115px;
  padding-bottom: 110px;
  padding-top: 105px;
  width: 100%;
  height: 100%;
  @include img;
  background-image: url('../img/teams-page/content-2.jpeg');
  @include r(768) {
    padding-left: 60px;
    @include r(580) {
      background-image: url('../img/teams-page/content-2-s.jpeg');
      padding-left: 25px;
      padding-top: 250px;
      padding-bottom: 60px; } } }

.block-2-title {
  h3 {
    padding-bottom: 60px;
    padding-top: 40px;
    max-width: 400px;
    @include r(580) {
      font-size: 3.2rem;
      padding-top: 20px;
      padding-bottom: 40px;
      max-width: 100%;
      padding-right: 20px; } } }
