.head__page-experts {
  padding-top: 140px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include r(768) {
    padding-top: 100px; }


  .breadcrumbs__container {
    a {
      color: #000; }
    li {
      color: #000; } }
  .breadcrumbs-active:before {
    color: #000; } }

.head__page-experts-wrapper {
  padding-left: 40px;
  padding-bottom: 140px;
  width: 100%;
  height: 110%;
  position: relative;
  @include r(1070) {
    padding-bottom: 120px;
    padding-right: 20px;
    @include r(768) {
      padding-left: 20px;
      padding-bottom: 80px;
      @include r(580) {
        padding-bottom: 60px; } } }

  &::before {
    content: '';
    position: absolute;
    bottom: 160px;
    width: 100%;
    max-width: 440px;
    height: 100%;
    max-height: 440px;
    @include img;
    background-image: url('../img/experts-page/arrows.svg');
    @include r(1070) {
      max-width: 270px;
      max-height: 270px;
      bottom: 140px;
      @include r(580) {
        bottom: 0;
        top: 45px;
        right: 20px; } } } }


.head__page-experts-content {
  width: 100%;
  max-width: 930px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  z-index: 1;
  @include r(900) {
    padding-left: 0;
    padding-right: 0; } }


.head__page-experts-title {
  width: 100%;
  padding-top: 60px;
  @include r(580) {
    padding-top: 40px; }
  h1 {
    color: #000;
    padding-bottom: 60px;
    text-align: center;
    @include r(1070) {
      font-size: 5rem;
      padding-bottom: 50px;
      max-width: 700px;
      margin: 0 auto;
      @include r(900) {
        font-size: 4.6rem;
        padding-bottom: 40px;
        @include r(768) {
          padding-top: 0px;
          font-size: 4rem;
          max-width: 600px;
          @include r(580) {
            font-size: 3.6rem;
            text-align: left;
            max-width: 350px;
            margin-left: 0; } } } } } }

.head__page-experts-subtitle {
  max-width: 665px;
  width: 100%;
  margin-left: auto;
  @include r(900) {
    max-width: 465px;
    @include r(768) {
      max-width: 400px;
      @include r(580) {
        max-width: 100%; } } }
  p {
    color: #000;
    font-size: 2.4rem;
    line-height: 200%;
    @include r(1070) {
      font-size: 2rem;
      @include r(900) {
        font-size: 1.6rem; } } }

  span {
    display: block;
    max-width: 450px;
    padding-top: 40px;
    @include fontFR;
    font-size: 1.8rem;
    line-height: 120%;
    letter-spacing: 0.12em;
    color: rgba(#000, .3);
    @include r(1070) {
      font-size: 1.6rem;
      padding-top: 30px;
      @include r(900) {
        max-width: 350px;
        font-size: 1.4rem;
        @include r(580) {
          padding-top: 20px; } } } } }


