
.head__page-teams {
  display: block;
  padding-top: 280px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include img;
  background-image: url('../img/teams-page/head-img.jpeg');
  background-position: left;
  @include r(768) {
    padding-top: 100px;
    background-position: center; } }

.head__page-teams-wrapper {
  padding-left: 40px;
  padding-bottom: 40px;
  padding-top: 0px;
  width: 100%;
  height: 110%;
  @include r(768) {
    padding-left: 25px;
    padding-bottom: 20px;
    @include r(580) {
      padding-bottom: 40px;
      padding-left: 20px; } } }

.head__page-teams-title {
  h1 {
    width: 100%;
    max-width: 630px;
    padding-bottom: 200px;
    @include r(768) {
      max-width: 450px;
      padding-top: 100px;
      @include r(580) {
        padding-right: 20px;
        @include r(380) {
          font-size: 3rem; } } }
    span {
      display: block; } } }

.head__page-teams-arrows {
  a {
    display: block;
    text-decoration: none;
    width: 100%;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    text-align: center;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #FFFFFF;
    padding-bottom: 5px;
    @include r(768) {
      font-size: 1.2rem; } }

  p {
    width: 100%;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    text-align: center;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #FFFFFF;
    padding-bottom: 5px;
    @include r(768) {
      font-size: 1.2rem; } }

  span {
    display: block;
    width: 20px;
    height: 25px;
    margin: 0 auto;
    position: relative;
    .svg-1 {
      position: absolute;
      top: 0;
      left: 0; }
    .svg-2 {
      position: absolute;
      bottom: -5px;
      left: 0; }

    img {
      width: 20px;
      height: 25px; } } }

.head__page-teams-arrows {
  .down {
    display: block;
    @include r(768) {
      display: none; } }

  .up {
    display: none;
    @include r(768) {
      display: block;
      padding-bottom: 10px; } } }

.head__page-teams-arrows {
  svg {
    @include r(768) {
      transform: rotate(180deg); } }
  path {
    fill: #fff;
    animation: arrow 2s infinite;
    -webkit-animation: arrow 2s infinite;
    transition: opacity 1s; }
  .a1 {
    animation-delay: -1s; }
  .a2 {
    animation-delay: -0.5s; } }

@keyframes arrow {
  0% {
    opacity: 0; }
  40% {
    opacity: 1; }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }

.head__page-teams {
  .breadcrumbs__container {
    display: none;
    @include r(768) {
      display: block; } } }

.breadcrumbs__container {
  ol {
    display: flex; }
  li {
    padding-right: 5px; }
  a {
    display: inline;
    font-size: 1.4rem;
    line-height: 120%;
    text-decoration: none;
    @include fontFR;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #FFFFFF;
    @include r(400) {
      font-size: 1.2rem; } }

  .breadcrumbs-active {
    font-size: 1.4rem;
    line-height: 120%;
    text-decoration: none;
    @include fontFR;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #FFFFFF;
    text-decoration: underline;
    @include r(400) {
      font-size: 1.2rem; } } }

