
.teams-block-6 {
  width: 100%;
  height: 100%;
  max-width: 100%;
  padding: 40px;
  padding-top: 0;
  @include r(768) {
    padding: 20px;
    padding-top: 0; } }

.block-6-wrapper {
  padding-left: 115px;
  padding-bottom: 110px;
  padding-top: 105px;
  width: 100%;
  height: 100%;
  @include img;
  background-image: url('../img/about-format-page/content-6.jpeg');
  @include r(840) {
    padding-left: 60px;
    @include r(768) {
      padding-left: 30px;
      @include r(580) {
        padding-left: 25px;
        padding-top: 250px;
        padding-bottom: 60px; } } } }

.block-6-title {
  h3 {
    padding-bottom: 60px;
    padding-top: 40px;
    max-width: 520px;
    @include r(768) {
      max-width: 350px;
      @include r(580) {
        max-width: 320px;
        font-size: 3.2rem;
        padding-top: 20px;
        padding-bottom: 40px;
        padding-right: 20px;
        @include r(380) {
          font-size: 3rem; } } } } }

