.model__bike-page-main {
  padding-top: 115px;
  padding-bottom: 40px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  @include r(768) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 80px; } }


.model__bike-wrapper {
  display: flex;
  padding-bottom: 60px;
  @include r(768) {
    padding-bottom: 40px;
    @include r(580) {
      flex-direction: column; } } }

.model__bike-title {
  max-width: 400px;
  margin-right: 65px;
  h3 {
    color: #000; } }



.model__bike-subtitle {
  color: #000;
  max-width: 420px; }




.model__bike-baner {
  width: 100%;
  height: auto;
  @include img;
  background-image: url('../img/main-page/banner-foto-1.jpeg');
  padding-top: 120px;
  padding-bottom: 130px;
  padding-left: 120px;
  @include r(1270) {
    padding-left: 60px;
    @include r(1100) {
      padding-top: 100px;
      padding-bottom: 100px;
      padding-left: 20px;
      background-position: inherit;
      @include r(920) {
        background-position: center;
        @include r(768) {
          padding-bottom: 60px; } } } } }

.model__bike-baner-title {
  h3 {
    max-width: 540px;
    padding-bottom: 70px;
    @include r(1100) {
      font-size: 5.4rem;
      padding-bottom: 50px;
      @include r(920) {
        font-size: 4rem;
        max-width: 440px;
        @include r(700) {
          font-size: 3rem;
          max-width: 340px; } } } } }
