.face__page-about-format {
  width: 100%;
  height: 100%;
  max-width: 100%;
  padding-top: 130px;
  padding-bottom: 95px;
  padding-left: 25px;
  padding-right: 25px;
  @include r(1070) {
    padding-top: 60px;
    @include r(768) {
      padding-bottom: 60px;
      padding-left: 5px;
      padding-right: 5px;
      @include r(380) {
        padding-bottom: 20px; } }

    .col-sm-6 {
      padding-bottom: 40px;
      @include r(380) {
        padding-bottom: 30px; } } }


  .page-main-title {
    color: #000;
    max-width: 700px;
    padding-bottom: 75px;
    @include r(1070) {
      padding-bottom: 50px;
      @include r(768) {
        max-width: 450px;
        @include r(580) {
          max-width: 100%;
          text-align: center;
          @include r(400) {
            font-size: 2.9rem; } } } } } }


.face__page-about-format-img {
  margin-bottom: 20px;
  @include r(575) {
    max-height: 375px; }
  img {
    width: 100%;
    height: auto; } }

.face__page-about-format-address {
  @include fontFR;
  font-size: 1.4rem;
  line-height: 150%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.5);
  @include r(1240) {
    font-size: 1.2rem;
    @include r(1140) {
      font-size: 1rem; } } }

.face__page-about-format-name {
  @include fontSR;
  font-size: 2.4rem;
  line-height: 170%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  @include r(1240) {
    font-size: 2rem;
    @include r(1140) {
      font-size: 1.8rem;
      @include r(880) {
        font-size: 1.6rem; } } } }

.face__page-about-format-text {
  @include fontSR;
  font-size: 1.6rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  @include r(1240) {
    font-size: 1.4rem;
    @include r(1140) {
      font-size: 1.2rem; } } }


.col-page-about-format {
  @include r(575) {
    max-width: 375px;
    margin: 0 auto; } }



