.video__page-main {
  width: 100%;
  @include img;
  background-image: url('../img/main-page/content-2.jpeg');
  padding: 260px 0;
  display: flex;
  justify-content: center;
  @include r(768) {
    padding-top: 200px;
    padding-bottom: 200px;
    @include r(580) {
      padding-top: 250px;
      padding-bottom: 60px;
      padding-left: 30px;
      justify-content: flex-start;
      @include r(360) {
        padding-right: 20px;
        padding-left: 20px; } } }






  .page-main-title {
    letter-spacing: .12em;
    text-align: center;
    @include r(580) {
      text-align: left;
      width: 100%;
      @include r(420) {
        font-size: 3rem;
        @include r(360) {
          font-size: 2.6rem; } } } } }


.video__page-main-subtitle {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  @include r(580) {
    text-align: left;
    @include r(360) {
      font-size: 1.4rem; } } }

.video__page-main-btn {
  max-width: 190px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  @include r(580) {
    margin-top: 40px;
    margin-left: 0;
    margin-right: 0;
    @include r(360) {
      margin-top: 25px;
      max-width: 150px; } }
  .btn {
    @include r(360) {
      font-size: 1.4rem;
      padding: 12px 20px; } } }
