
.story-page-main {
  width: 100%;
  padding-top: 100px;
  padding-bottom: 120px;
  padding-left: 34px;
  display: flex;
  position: relative;
  @include r(768) {
    padding-bottom: 60px;
    @include r(580) {
      flex-direction: column;
      padding-top: 40px;
      padding-bottom: 60px;
      padding-left: 10px;
      padding-right: 10px; } } }


.story-content-wrapper {
  width: 100%;
  max-width: 380px;
  margin-right: 100px;
  @include r(580) {
    margin-right: 0; } }




.story-title {
  h3 {
    color: #000;
    @include r(1040) {
      font-size: 5.2rem;
      @include r(768) {
        font-size: 4rem;
        @include r(580) {
          padding-bottom: 40px;
          font-size: 3.6rem;
          max-width: 300px;
          padding-left: 15px;
          @include r(400) {
            padding-left: 0; } } } } } }

.story-subtitle {
  @include r(580) {
    display: none; }
  p {
    color: #000;
    @include r(1040) {
      font-size: 1.5rem;
      @include r(768) {
        font-size: 1.3rem;
        padding-top: 20px; } } } }


.story-baner-btn {
  color: #000;
  border: 2px solid #000;
  max-width: 200px;
  margin-top: 50px;
  @include r(920) {
    max-width: 150px;
    margin-top: 40px;
    @include r(580) {
      display: none; } }
  a {
    color: #000;
    @include r(1040) {
      font-size: 1.5rem;
      text-align: center;
      @include r(920) {
        font-size: 1.4rem;
        padding-left: 18px;
        padding-right: 18px; } }
    &:hover {
      background-color: rgba(#000, .1); } } }


.story-wrapper {
  width: 30%;
  @include r(1140) {
    margin-right: 30px;
    @include r(580) {
      margin-right: 0;
      width: 100%;
      display: flex;
      justify-content: center; } } }


.story-wrap {
  width: 70%;
  @include r(1140) {
    width: 66%;
    @include r(580) {
      width: 100%; } } }


.story-container {
  width: 100%;
  display: flex;
  @include r(1500) {
    max-width: 1000px;
    @include r(768) {
      max-width: 352px; } } }

.story-slide {
  max-width: 440px;
  @include r(768) {
    max-width: 370px;
    margin-right: 0; } }

.story-slide-img {
  a {
    display: block; }
  img {
    width: 100%; } }


.story-slide-title {
  a {
    display: block;
    text-decoration: none;
    padding-top: 27px;
    padding-bottom: 20px;
    @include fontSR;
    font-size: 2.4rem;
    line-height: 150%;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #000000;
    padding-right: 100px;
    @include r(1240) {
      padding-right: 20px;
      @include r(580) {
        padding-right: 0;
        padding-top: 20px; } } } }

.story-slide-subtitle {
  padding-bottom: 20px;
  @include fontFR;
  font-size: 1.8rem;
  line-height: 120%;
  letter-spacing: 0.12rem;
  color: #000000;
  padding-right: 100px;
  @include r(580) {
    padding-right: 0; } }

.story-slide-btn {
  a {
    text-decoration: underline;
    border: none;
    color: #000;
    padding: 0;
    @include r(768) {
      text-align: left; } } }





.swiper-button-next,
.swiper-button-prev {
  right: 40px;
  top: 80px;
  left: auto;
  width: 11px;
  height: 11px;
  background-size: 11px 11px;
  border: 1px solid #000;
  padding: 10px 22px;
  @include r(768) {
    right: 65px;
    @include r(580) {
      display: none; } } }



.swiper-button-prev {
  right: 85px;
  background-image: url('../img/main-page/icons/arrow-left.svg');
  @include r(768) {
    right: 110px; } }

.swiper-button-next {
  background-image: url('../img/main-page/icons/arrow-right.svg'); }



.swiper-pagination-story {
  display: none;
  @include r(580) {
    display: block;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%);
    z-index: 1; }

  .swiper-pagination-bullet {
    background-color: #F5F5F5;
    opacity: 1;
    border-radius: 0;
    @include r(768) {
      height: 6px;
      width: 68px;
      @include r(410) {
        width: 40px;
        height: 4px; } } }


  .swiper-pagination-bullet-active {
    background-color: #000; } }


.swiper-button-disabled {
  display: none;
  @include r(1500) {
    display: block;
    @include r(580) {
      display: none; } } }
