


.tegnology__page-main {
  width: 100%;
  @include img;
  background-image: url('../img/main-page/banner-foto-3.jpeg');
  padding-top: 120px;
  padding-bottom: 130px;
  padding-left: 155px;
  @include r(980) {
    padding-left: 80px;
    @include r(768) {
      padding-left: 40px;
      padding-top: 160px;
      padding-bottom: 160px;
      @include r(410) {
        padding-right: 20px;
        padding-left: 20px; } } } }




.tegnology__page-main-title {
  h3 {
    max-width: 750px;
    letter-spacing: 0.12em;
    padding-bottom: 60px;
    @include r(768) {
      max-width: 400px;
      padding-bottom: 40px;
      @include r(380) {
        font-size: 3rem; } } } }

.tegnology__page-main-btn {
  .btn {
    @include r(380) {
      max-width: 160px;
      padding: 11px 20px;
      font-size: 1.4rem; } } }


