.back-shopping-wrapper {
  padding-top: 60px;
  padding-bottom: 60px; }

.shoping {
  display: block;
  cursor: pointer;
  text-decoration: none;
  width: 100%;
  max-width: 310px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 30px;
  padding-left: 60px;
  text-align: center;
  border: 2px solid #000000;
  position: relative;
  transition: all .3s;
  @include r(580) {
    max-width: 240px;
    padding-left: 30px;
    padding-right: 20px; }

  &:hover {
    background-color: rgba(#000, .1);
    text-decoration: underline; }

  &::before {
    content: '';
    position: absolute;
    top: (calc( 50% - 6px ));
    left: 30px;
    width: 8px;
    height: 12px;
    @include img;
    background-image: url('../img/compare-page/arrow-left.svg');
    @include r(580) {
      left: 15px; } } }


.delete {
  display: block;
  cursor: pointer;
  border: none;
  outline: none;
  width: 100%;
  max-width: 170px;
  margin: 0 auto;
  padding-top: 30px;
  padding-left: 20px;
  background-color: transparent;
  text-decoration: underline;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #000;
    @include img;
    background-image: url('../img//compare-page/delete.svg');
    background-size: 10px 10px; } }
