.page-history-2004 {
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: flex-end;
  position: relative;
  @include r(768) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 40px;
    padding-bottom: 40px; } }


.page-history-wrapper {
  width: 100%;
  max-width: 940px;
  @include r(1140) {
    max-width: 700px;
    @include r(920) {
      max-width: 550px;
      @include r(768) {
        max-width: 100%; } } } }


.page-history-year {
  display: block;
  @include fontSR;
  font-size: 2.4rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  position: absolute;
  top: 65px;
  left: 40px;
  @include r(920) {
    top: 55px;
    font-size: 2.2rem;
    @include r(768) {
      position: relative;
      top: 0;
      left: 0;
      padding-bottom: 40px;
      font-size: 2rem;
      @include r(580) {
        font-size: 1.8rem; } } } }

.page-history-text {
  @include fontSR;
  font-size: 4.8rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  @include r(1140) {
    font-size: 3.5rem;
    @include r(920) {
      font-size: 2.8rem;
      @include r(580) {
        font-size: 2.2rem; } } } }


.page-history-img-wrapper {
  padding-top: 90px;
  max-width: 670px;
  margin-bottom: 20px;
  margin-left: auto;
  @include r(1140) {
    max-width: 600px;
    padding-top: 60px;
    @include r(920) {
      max-width: 500px;
      padding-top: 40px;
      @include r(768) {
        max-width: 100%; } } } }



.page-history-img {
  @include r(768) {
    max-width: 450px;
    margin: 0 auto; }
  img {
    width: 100%;
    height: auto; } }

.page-history-img-text-small {
  display: block;
  @include fontFR;
  font-size: 1.8rem;
  line-height: 150%;
  letter-spacing: 0.1em;
  color: rgba(0, 0, 0, 0.5);
  padding-top: 20px;
  @include r(768) {
    max-width: 450px;
    margin: 0 auto;
    padding-top: 10px;
    @include r(580) {
      font-size: 1.4rem; } } }

.page-history-img-text {
  padding-top: 40px;
  p {
    @include fontSR;
    font-size: 2.4rem;
    line-height: 200%;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #000000;
    padding-bottom: 40px;
    @include r(768) {
      padding-bottom: 20px;
      font-size: 1.8rem;
      @include fontFR; }

    &:last-child {
      padding-bottom: 0; } } }




// SELECT

.select__year__top {
  display: block; }

.select-year {

  display: none;
  cursor: pointer;
  position: relative;
  align-items: center;
  border-bottom: 1px solid #000;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 60px;

  @include r(768) {
    display: flex; }

  & a {
    display: block;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #000;
    height: 100%;
    text-decoration: none;
    transition: all .5s;
    padding-left: 15px;
    @include r(1070) {
      font-size: 1.4rem; } } }


.select__body-year {
  display: none;
  position: absolute;
  top: 100%;
  width: 100%;
  left: 0;
  z-index: 2;
  border: 1px solid #000;
  background-color: #eee; }


.link__year {
  position: relative;
  padding: 10px 0;
  box-sizing: border-box; }



// js-select
.select-year.is-active .select__body-year {
  display: block; }

.select__item-phone.remove {
  display: none; }

.select__item-year.remove {
  display: none; }

.select-year__top {
  @include r(768) {
    position: relative;
    width: 100%; } }

.select-year__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 5px );
  right: 10px;
  width: 10px;
  height: 10px;
  background-image: url('../img/history-page/arrow-select.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: transform .4s; }

.select-year.is-active .select-year__top:before {
  transform: rotate(180deg); }
