
.head__page-main {
  display: block;
  padding-top: 240px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include img;
  background-image: url('../img/main-page/head-img.jpeg');
  @include r(768) {
    padding-top: 200px;
    @include r(420) {
      padding-top: 165px; } } }



.head__page-main-wrapper {
  padding-left: 40px;
  padding-bottom: 40px;
  padding-top: 0px;
  width: 100%;
  height: 110%;
  @include r(768) {
    padding-left: 25px;
    padding-bottom: 20px;
    @include r(580) {
      padding-bottom: 40px; } } }

.head__page-main-title {
  h1 {
    width: 100%;
    max-width: 500px;
    @include r(768) {
      max-width: 350px; }
    span {
      @include r(580) {
        display: block; } } } }




.page-main-title {
  @include fontSR;
  font-size: 6.4rem;
  line-height: 102%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding-bottom: 30px;
  @include r(768) {
    font-size: 4.4rem;
    padding-bottom: 20px;
    @include r(580) {
      font-size: 3.6rem; } } }





.head__page-main-subtitle {
  padding-bottom: 50px;
  @include r(580) {
    padding-bottom: 40px; } }

.page-main-subtitle {
  @include fontSR;
  font-size: 1.8rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  @include r(768) {
    font-size: 1.4rem;
    @include r(580) {
      font-size: 1.8rem; } } }



.head__page-main-btn {
  margin-bottom: 190px;
  @include r(768) {
    margin-bottom: 120px;
    @include r(580) {
      margin-bottom: 190px; } } }

.btn {
  text-decoration: none;
  display: block;
  outline: none;
  width: 100%;
  max-width: 205px;
  padding: 14px 30px;
  border: 2px solid #FFFFFF;
  @include fontSR;
  font-size: 1.6rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer;
  transition: background .6s;
  @include r(768) {
    max-width: 170px;
    padding: 12px 20px;
    font-size: 1.4rem;
    text-align: center;
    @include r(580) {
      padding: 14px 25px;
      max-width: 200px;
      font-size: 1.6rem; } }
  &:hover {
    background: rgba(255,255,255,.3); } }




.head__page-main-arrows, .head__page-history-arrows {
  width: 100%;
  a {
    display: block;
    text-decoration: none;
    width: 100%;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    text-align: center;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #FFFFFF;
    padding-bottom: 5px;
    @include r(768) {
      font-size: 1.2rem; } }

  p {
    width: 100%;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    text-align: center;
    letter-spacing: 0.12rem;
    text-transform: uppercase;
    color: #FFFFFF;
    padding-bottom: 5px;
    @include r(768) {
      font-size: 1.2rem; } }

  span {
    display: block;
    width: 20px;
    height: 25px;
    margin: 0 auto;
    position: relative;
    .svg-1 {
      position: absolute;
      top: 0;
      left: 0; }
    .svg-2 {
      position: absolute;
      bottom: -5px;
      left: 0; }

    img {
      width: 20px;
      height: 25px; } } }


.head__page-main, .head__page-history {
  .down {
    display: block;
    @include r(768) {
      display: none; } }

  .up {
    display: none;
    @include r(768) {
      display: block;
      padding-bottom: 10px; } } }




.head__page-main-arrows, .head__page-history-arrows {
  svg {
    @include r(768) {
      transform: rotate(180deg); } }
  path {
    fill: #fff;
    animation: arrow 2s infinite;
    -webkit-animation: arrow 2s infinite;
    transition: opacity 1s; }
  .a1 {
    animation-delay: -1s; }
  .a2 {
    animation-delay: -0.5s; } }


@keyframes arrow {
  0% {
    opacity: 0; }
  40% {
    opacity: 1; }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }




