


.advertising__page-main {
  padding: 40px;
  @include r(580) {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 20px;
    padding-bottom: 20px; } }


.advertising__page-main-wrapper {
  width: 100%;
  @include img;
  background-image: url('../img/main-page/banner-foto-2.jpeg');
  padding-top: 90px;
  padding-left: 115px;
  padding-bottom: 130px;
  @include r(768) {
    padding-left: 40px;
    background-position: right;
    @include r(580) {
      background-position: top;
      padding-left: 20px;
      padding-top: 250px;
      padding-bottom: 60px;
      @include r(380) {
        background-position: right; } } } }



.advertising__page-main-title {
  h3 {
    max-width: 510px;
    letter-spacing: 0.12em;
    @include r(768) {
      max-width: 350px;
      padding-bottom: 40px;
      @include r(420) {
        font-size: 3.2rem; } } } }




