
.compare-page {
  padding-top: 120px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  @include r(980) {
    padding-top: 130px;
    padding-left: 20px;
    padding-right: 20px;
    @include r(768) {
      padding-top: 105px; } }

  .breadcrumbs__container {
    padding-bottom: 65px;
    @include r(768) {
      padding-bottom: 40px; }
    li {
      color: #000;
      @include r(400) {
        font-size: 1rem;
        padding: 5px; } }
    a {
      color: #000;
      @include r(400) {
        font-size: 1rem; } }
    .breadcrumbs-active {
      color: #000;
      @include r(400) {
        font-size: 1rem; } } }


  .container-fluid {
    padding: 0; }

  .col {
    padding: 0; } }




.compare-page-title {
  color: #000;
  padding-bottom: 40px;
  @include r(980) {
    font-size: 5.4rem;
    @include r(768) {
      font-size: 3.6rem; } } }

.compare-img-wrap {
  position: relative;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  img {
    width: 100%;
    height: auto; }

  .compare-remove {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    @include fontSM;
    font-size: 1rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration: underline;
    text-transform: uppercase;
    color: #000000;
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
    @include r(768) {
      font-size: 1.2rem;
      top: 88%;
      right: calc(50% - 30px);
      @include r(580) {
        top: 84%;
        @include r(420) {
          top: 80%; } } } } }




.compare-wrap--padding-bottom {
  padding-top: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  white-space: normal; }

.compare-title {
  @include fontSM;
  font-weight: bolder;
  font-size: 1.4rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 30px; }


.compare-small-title {
  @include fontSR;
  font-size: 1.6rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 10px;
  padding-right: 20px;
  @include r(768) {
    font-size: 1.4rem; } }

.compare-small-subtitle {
  @include fontFR;
  font-size: 1.8rem;
  line-height: 150%;
  letter-spacing: 0.1em;
  color: #000000;
  padding-bottom: 30px;
  width: 100%;
  max-width: 325px;
  padding-right: 20px;
  @include r(768) {
    font-size: 1.4rem; } }

.compare-color-down {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  margin-bottom: 30px; }


.compare-color-down-black {
  background-color: #000; }

.compare-color-down-red {
  background-color: #ff0000; }

.img-wrap {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: auto; } }

.price {
  display: block;
  text-align: center;
  padding-top: 30px; }

.compare-see {
  display: block;
  @include fontSM;
  font-size: 1.6rem;
  line-height: 105%;
  letter-spacing: 0.12em;
  text-decoration: underline;
  text-transform: uppercase;
  color: #000000;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  padding-bottom: 30px;
  width: 100%;
  max-width: 150px;
  margin: 0 auto;
  @include r(768) {
    font-size: 1.4rem; } }





.row-scroll {}



.item-row {
  display: inline-flex; }

.item-wrapper {
  display: inline-block;
  width: calc( (100vw - 80px) / 4);
  height: 100%;
  @include r(980) {
    width: calc( (100vw - 40px) / 3);
    @include r(768) {
      width: calc( (100vw - 40px) / 2); } } }





//model FIXED

.scroll-fixed {
  display: none;

  &::before {

    content: '';
    position: fixed;
    top: 75px;
    left: 0;
    height: 60px;
    width: 100%;
    background-color: #F5F5F5;
    z-index: 1;
    padding: 0 20px; } }

.scroll-fixed.fixed {
  @include r(768) {
    display: flex; } }






.model-fixed-text {
  position: relative;
  width: 50%;
  padding-left: 30px;
  font-size: 1.8rem;
  padding-bottom: 0;
  padding-right: 0;
  margin-top: 30px;
  &::before {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    top: 3px;
    left: 0;
    @include img;
    background-image: url('../img/compare-page/arrow-mob.svg'); } }

.compare-page .container-fluid .row-scroll .item-row .item-wrapper .model-fixed-text.active {
  position: fixed;
  top: 80px;
  z-index: 4; }




.os-scrollbar-horizontal {
  display: none; }


.os-scrollbar-horizontal.active {
  @include r(768) {
    display: block; } }

