.page-history-2006 {
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: flex-end;
  position: relative;
  @include r(768) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 40px;
    padding-bottom: 40px; } }

.page-history-2006-wrapper {
  @include r(1240) {
    max-width: 740px;
    @include r(1040) {
      max-width: 600px;
      @include r(920) {
        max-width: 490px;
        @include r(768) {
          max-width: 100%; } } } } }

.page-history-img-wrapper-2006 {
  max-width: 100%; }

.page-history-img-text {
  max-width: 650px;
  margin-left: auto;
  @include r(920) {
    max-width: 420px; } }
