// width
@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content; } }

@mixin rmin($width) {
  @media only screen and (min-width: $width+ "px") {
    @content; } }

// height
@mixin rh($height) {
  @media only screen and (max-height: $height + "px") {
    @content; } }

@mixin rhmin($height) {
  @media only screen and (min-height: $height + "px") {
    @content; } }







// flex in center
@mixin center {
  display: flex;
  justify-content: center;
  align-items: center; }
@mixin center-x {
  display: flex;
  justify-content: center; }
@mixin center-y {
  display: flex;
  align-items: center; }
@mixin center-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

// img
@mixin img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }


// fonts

@mixin fontSM {
  font-family: 'SM', Arial, sans-serif; }

@mixin fontSR {
  font-family: 'SR', Arial, sans-serif; }

@mixin fontFB {
  font-family: 'FB', Arial, sans-serif; }

@mixin fontFR {
  font-family: 'FM', Arial, sans-serif; }
