
.head__page-history {
  display: block;
  padding-top: 220px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include img;
  background-image: url('../img/history-page/head-img.jpeg');
  @include r(768) {
    padding-top: 100px;
    background-position: left;
    @include r(580) {
      background-image: url('../img/history-page/head-img-s.jpeg');
      background-position: center; } }

  .btn {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 180px;
    text-align: center;
    @include r(768) {
      margin-bottom: 160px;
      @include r(580) {
        max-width: 190px; } } }

  .breadcrumbs__container {
    display: none;
    @include r(768) {
      display: block; } } }


.head__page-history-wrapper {
  padding-bottom: 40px;
  padding-top: 0px;
  width: 100%;
  height: 110%;
  @include r(768) {
    padding-left: 25px;
    padding-bottom: 20px;
    @include r(580) {
      padding-bottom: 40px; } } }


.head__page-history-title {
  @include r(768) {
    padding-top: 160px; }
  h1 {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
    @include r(580) {
      max-width: 300px; } } }

.head__page-history-subtitle {
  max-width: 250px;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 40px;
  font-size: 2.4rem;
  @include r(580) {
    font-size: 1.6rem; } }
