@font-face {
    font-family: 'SM';
    src: url('../fonts/stolzldisplay-medium-webfont.woff2') format('woff2'),
         url('../fonts/stolzldisplay-medium-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}




@font-face {
    font-family: 'SR';
    src: url('../fonts/stolzldisplay-regular-webfont.woff2') format('woff2'),
         url('../fonts/stolzldisplay-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}





@font-face {
  font-family: 'FB';
  src: url('../fonts/FuturaPT-Bold.woff2') format('woff2'),
      url('../fonts/FuturaPT-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}


@font-face {
  font-family: 'FM';
  src: url('../fonts/FuturaPT-Light.woff2') format('woff2'),
      url('../fonts/FuturaPT-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

