.footer {
  padding-top: 68px;
  padding-left: 40px;
  padding-right: 70px;
  background-color: #000;
  @include r(1040) {
    padding-left: 20px;
    padding-right: 10px; }
  .row {
    @include r(995) {
      justify-content: space-between; } } }

.footer__arrows {
  @include r(768) {
    margin-bottom: 70px; } }

.footer__item {
  margin-bottom: 85px;
  @include r(768) {
    margin-bottom: 70px; } }

.footer__item-title {
  @include fontSR;
  font-size: 1.8rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 16px; }

.footer__link {
  display: block;
  @include fontFR;
  text-decoration: none;
  font-size: 1.8rem;
  line-height: 120%;
  letter-spacing: 0.12em;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 10px;
  transition: color .3s;
  &:hover {
    color: #fff; } }

.footer__botoom-icons {
  svg {
    path {
      fill: #808080;
      transition: fill .4s; } } }

.footer__botoom-icons {
  a:hover svg path {
    fill: #fff; } }







// footer bottom
.footer__botoom {
  background-color: #000;
  padding: 25px 40px;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  @include r(940) {
    padding: 15px 20px;
    @include r(768) {
      border-top: none;
      padding: 50px 0 0 0; } } }

.footer__botoom-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include r(768) {
    flex-direction: column-reverse; } }

.footer__botoom-copiraty {
  @include r(768) {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding-top: 16px;
    padding-bottom: 21px;
    width: 100%;
    text-align: center; } }


.footer__botoom-text {
  @include fontFR;
  font-size: 1.4rem;
  line-height: 120%;
  letter-spacing: 0.12em;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  @include r(940) {
    font-size: 1.2rem; } }





.footer__botoom-link {
  @include r(768) {
    margin-top: 25px;
    margin-bottom: 25px; }
  a {
    margin-left: 25px;
    transition: color .4s;
    @include r(940) {
      margin-left: 15px;
      @include r(768) {
        margin-left: 5px;
        padding-left: 10px;
        border-left: 1px solid rgba(255, 255, 255, 0.2); } }
    &:hover {
      color: #fff; }



    &:first-child {
      @include r(768) {
        border-left: none; } } } }



.footer__botoom-icons {
  a {
    display: inline-block;
    width: 30px;
    height: 30px;
    position: relative;
    margin-left: 20px;
    @include r(940) {
      margin-left: 10px;
      width: 25px;
      height: 25px; }
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0; } } }


