
.head__page-catalog {
  padding-top: 120px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include img;
  background-image: url('../img/catalog-page/head-img.jpg');
  background-position: top;
  @include r(768) {
    padding-top: 160px;
    padding-bottom: 140px; } }



.head__page-catalog-wrapper {
  padding-left: 40px;
  padding-bottom: 40px;
  padding-top: 0px;
  width: 100%;
  height: 110%;
  @include r(768) {
    padding-left: 20px;
    padding-bottom: 20px; } }


.head__page-catalog-title {
  padding-top: 160px;
  @include r(768) {
    padding-top: 20px; }
  h1 {
    max-width: 500px;
    padding-bottom: 0;
    @include r(768) {
      max-width: 350px;
      font-size: 3.6rem; } } }
